import { AbstractFilter } from './abstract_filter'

export class Type extends AbstractFilter {

    private type: string;

    constructor(type: string) {
        super();
        this.type = type;
    }

    public static make(type: string): Type {
        return new Type(type);
    }

    public toJSON() {
        return { type: { value: this.type } };
    }
}